
body{
  background-color: #f5f5f5;
  padding-top: 1rem;
}

.container{
  width: 100%;/*--definit la largeuer totale de la page*/
  max-width: 48rem;/*--definit la largeur utile de la page*/
  margin: 0 auto;/*--Centre la marge utitle par rapport à la marge totale*/
  font: 1.6rem/1.25 Arial, sans-serif; /*--définit la taille, l'interligne et le style de la police*/
  background-color: #ffffff;
  color: #4d4d4d;/**/
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2), 0 2.5rem 5rem 0 rgba(0, 0, 0, 0.1);/**/
  padding-top: 1rem;
  padding-bottom: 1.5rem;
}

.memboList{
  margin: 0 auto;
}

.underline{
  text-decoration: underline;
}

.subscript{
  margin: 0 auto;
}

.connect{
  border-radius: 20rem;  
  height: 1rem;
}

img[class="avatar"]{
  border-radius: 20rem;  
  width: 100%;
  min-width: 70%;
  position: relative;
}

.member{
  cursor: pointer;
  border: 0.2rem solid #4d4d4d;
  border-radius: 10px;
  padding: 0 1rem 0 1rem;
  margin: 0.3rem 0.3rem 0.3rem 0.3rem;
  width: 20%;
}

.avata{
  background-color: rgba(128, 128, 128, 0.37);
  border-radius: 18rem;  
  height: 100%;
  min-width: 5rem;
}

.tyty{
  background-color: rgba(128, 128, 128, 0.37);
}

.stater{
  background-color: rgba(128, 128, 128, 0.37);
  display: flex;
}